<template>
  <div class='main'>
    <div class='contain df'>
      <div class='contain-main flex1'>
        <div class='search'>
          <el-form :inline='true' class='demo-form-inline' size='small'>
            <el-form-item>
              <el-select :value="statusSelect" @change="selectReadChange">
                <el-option label='全部消息' value='0'></el-option>
                <el-option label='未读消息' value='1'></el-option>
                <el-option label='已读消息' value='2'></el-option>
              </el-select>
            </el-form-item>
            <el-form-item>
              <el-select :value="typeSelect" placeholder='请选择消息类型' @change="selectTypeChange">
                <el-option label='全部' value='0'></el-option>
                <el-option label='待审核' value='1'></el-option>
                <el-option label='审核通过' value='2'></el-option>
                <el-option label='审核不通过' value='3'></el-option>
              </el-select>
            </el-form-item>
