          <el-button type='default' size='small' @click="deleteReaded">删除</el-button>
        </div>
        <div class='page-main mt20'>
          <el-table
            :data='tableData4'
            stripe
            border
            type='selection'
            style='width: 100%'
            @select="select"
            @select-all="select"
          >
            <el-table-column
              type='selection'
              fixed='left'
              width='40'>
