                <a class='no-decoration' :href='scope.row.src'
                   v-if="item.link"
                   @click='handleTitleClick(scope.row)'>{{scope.row.title}}</a>
                <span v-else>{{scope.row[item.key]}}</span>
              </template>
            </el-table-column>
            <el-table-column
              fixed='right'
              prop='zip'
              label='操作'
              width='160'>
              <template slot-scope='scope'>
                <a class='no-decoration' href='javascript:void(0);'
                   @click='handleClick(scope.row)'>删除</a>
              </template>
            </el-table-column>
          </el-table>
          <div class='page-wrapper'>
            <el-pagination
              @size-change='handleSizeChange'
              @current-change='handleCurrentChange'
              :current-page='currentPage'
              :page-sizes='[20, 50,100]'
              :page-size='20'
              layout='total, sizes, prev, pager, next, jumper'
              :total='tableParams.total'>
            </el-pagination>
          </div>
