        typeSelect: '',
        currentPage: 1,
        selectDict: {
          read: ['all', 'read', 'readed'],
          type: ['all', '待审核', '审核通过', '审核不通过']
        },
        statusSelect: '2',
        isSlide: false,
        columns: [
          {title: '标题', key: 'title', link: 'src'},
          {title: '发布时间', key: 'createAt'}
        ],
        data: [
          {
            label: '一级 1',
            children: [{
              label: '二级 1-1',
              children: [{
                label: '三级 1-1-1'
