                children: [{
                  label: '三级 3-2-1'
                }]
              }
            ]
          }],
        defaultProps: {
          children: 'children',
          label: 'label'
        }

      }
    },
    mixins: [tableMixin,],
    methods: {
      handleClick(row) {
        console.log(row)
        console.log('删除当前消息')
      },
      selectReadChange(selectLabel) {
        console.log(this.selectDict.read[selectLabel])
        console.log('请求')
      },
      selectTypeChange(selectLabel) {
        console.log(this.selectDict.type[selectLabel])
        console.log('请求')
      },
      select(array) {
        this.selectArr = array
        console.log(this.selectArr)
      },
      settingReaded() {
        console.log(this.selectArr)
        console.log('设置为已读')
      },
      deleteReaded() {
        console.log(this.selectArr)
        console.log('delete')
      },
      handleCurrentChange() {
        console.log('换页')
      },
      handleTitleClick(row) {
        console.log(row)
        console.log('跳转对应系统src')
      }
    },
  }
</script>
